.number {
  width: 31px;
  height: 31px;

  font-size: 20px;
  text-align: center;
  color: #1abc9c;
}

.name-music-chart {
  margin: 0;

  font-size: 14px;
  font-weight: 500;

  cursor: pointer;

  &:hover {
    color: #0689ba;
  }
}

.name-singer-chart {
  font-size: 13px;
  font-weight: 400;

  margin: 0;

  color: #a2a2a2;
  cursor: pointer;

  &:hover {
    color: #0689ba;
  }
}
